@import "../../global.scss";



#new {
    background: url("https://e0.pxfuel.com/wallpapers/945/947/desktop-wallpaper-spider-man-into-the-spider-verse-background.jpg")no-repeat top center;
    background-size: cover;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color:$mainColor;
}
.container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}
.product {
    width: 350px;
        height: 400px;
        background-color: rgb(108, 73, 189);
        border-radius: 20px;
        border: 1px solid rgb(174, 114, 243);
        margin: 10px 20px;
        display: flex;  
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: white;
        position: relative;
        text-align: center;
        transition: all 0.6s ease;
        cursor: pointer;
}
h3 {
    position: absolute;
    font-size: 20px;
}
img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 1;

}



      
 
    
 
    