@import "../../global.scss";


.intro {
    display: flex;
    background: linear-gradient(
        rgba(181, 169, 248, 0.5),
        rgba(14, 15, 22, 0.5)
      ),
    url(https://e0.pxfuel.com/wallpapers/1008/153/desktop-wallpaper-marvel-comics-comic-book-marvel-comics-marvel-black-and-white-comic.jpg);
    @include mobile{
        flex-direction: column;
        align-items: center;
    }


    .left {
        flex: 0.5;
        overflow: hidden;

        .imageContainer{
            width: 700px;
            height: 600px;
            background-color: rgb(243, 245, 112);
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            @include mobile {
                align-items: flex-start;
            }

            img{
                height: 90%;

            @include mobile {
                height: 50%;
            }
            }
        }
    }
    .right {
        flex: 0.5;
        position: relative;
        

        .wrapper {
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
            justify-content: center;

            @include mobile{
                padding-left: 0;
                align-items: center;
            }
          
             


            h1{
                font-size: 60px;
                margin: 10px 0;
                color: rgb(249, 250, 248);


                @include mobile{
                    font-size: 40px;
                }
            }
            h2{
                font-size: 35px;
                color: rgb(251, 252, 252);
            }
            h3{
                font-size: 30px;
                color:rgb(248, 245, 245);

                @include mobile{
                    font-size: 20px;
                }

                span{
                    font-size: inherit;
                    color:rgb(90, 255, 255);
                }
                .ityped-cursor{
                    animation: blink 1s infinite;
                }

                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                }
            }
        }
        a{
            position: absolute;
            bottom: 10px;
            left: 40%;
            
            img{
                width: 30px;
                animation: arrowBLink 2s infinite;
            }
            @keyframes arrowBLink {
                100%{
                    opacity: 0;
                }
            }
        }
    }
}